"use client"
import React, { useState, useEffect } from 'react'
import { useSession, signIn, signOut } from "next-auth/react"
import Link from 'next/link'
import { useRouter } from 'next/router'

const Navbar = () => {
    const { data: session } = useSession()
    const [showdropdown, setShowdropdown] = useState(false)

    return (
        <div className="bg-gray-900 text-white">
            <div className="bg-gray-900 shadow">
                <div className="container mx-auto px-4">
                    <div className="flex items-center justify-between py-2">
                        <div className='text-xl font-semibold flex items-center mr-16'>
                            <img className='mb-3' width={50} src="./tea.gif" alt="" />
                            <span>GetMeAChai</span>
                        </div>

                        <div className=" text-white sm:flex sm:items-center ">
                            <Link href="/" className="text-white transition-all text-sm font-semibold hover:text-gray-400 mr-5">Home</Link>
                            <Link href="#" className="text-white transition-all text-sm font-semibold hover:text-gray-400 mr-5">About</Link>
                            <Link href="#" className="text-white transition-all text-sm font-semibold hover:text-gray-400 mr-5">Projects</Link>
                        </div>

                        <div className="hidden sm:flex sm:items-center space-x-1 min-w-[220px] justify-end">
                            {session && <>
                                <button onClick={() => setShowdropdown(!showdropdown)} className="text-white flex justify-center items-center cursor-pointer bg-gradient-to-br from-purple-600 to-blue-500 hover:bg-gradient-to-bl focus:ring-2 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800 font-medium rounded-lg text-sm px-4 py-2 text-center  ">Wellcome {session.user.name} <svg className="w-2.5 h-2.5 ms-3" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 10 6">
                                    <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="m1 1 4 4 4-4" />
                                </svg>
                                </button>
                                <div id="dropdown" className={`absolute top-16  z-15 ${showdropdown ? "" : "hidden"} bg-white divide-y divide-gray-100 rounded-lg shadow-sm w-44 dark:bg-gray-700`}>
                                    <ul className="py-2 text-sm text-gray-700 dark:text-gray-200" aria-labelledby="dropdownDefaultButton">
                                        <li>
                                            <Link href={"/dashboard"} className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">Dashboard</Link>
                                        </li>
                                        <li>
                                            <Link href="#" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">Settings</Link>
                                        </li>
                                        <li>
                                            <Link href="#" className="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">Earnings</Link>
                                        </li>
                                        <li>
                                            <Link href="#" onClick={() => { signOut() }} className=" block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">Sign out</Link>
                                        </li>
                                    </ul>
                                </div>
                            </>}
                            {/* {session && <Link href={"/dashboard"}><button type="button" className="text-white cursor-pointer bg-gradient-to-br from-purple-600 to-blue-500 hover:bg-gradient-to-bl focus:ring-4 focus:outline-none focus:ring-blue-300 dark:focus:ring-blue-800 font-medium rounded-lg text-sm px-4 py-2 text-center me-2 ">Dashboard</button></Link>} */}
                            {/* {session && <Link href={"/dashboard"}><button className="text-black cursor-pointer bg-white transition-all text-sm font-semibold border px-4 py-2 rounded-lg hover:border-purple-600" onClick={() => { signOut() }}>Sign out</button></Link>} */}
                            {!session && <Link href={"/login"}>
                                <button className="text-black cursor-pointer bg-white transition-all text-sm font-semibold border px-4 py-2 rounded-lg ml-20 hover:border-purple-600" >Login</button></Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Navbar
